import {useToast} from "vue-toastification";
import router from "@/router";
import store from "@/store";

const toast = useToast();

const notification = {
    params: {
        position: "top-center",
        timeout: 5000,
        closeOnClick: true,
        pauseOnFocusLoss: true,
        pauseOnHover: true,
        draggable: true,
        draggablePercent: 0.6,
        showCloseButtonOnHover: false,
        hideProgressBar: false,
        closeButton: "button",
        icon: true,
        rtl: false
    },
    successNotification(app, msg) {
        toast.success(msg, this.params);
    },
    errorNotification(app, msg) {
        toast.error(msg, this.params);
    },
    warningNotification(app, msg) {
        toast.warning(msg, this.params);
    },
    infoNotification(app, msg) {
        toast.info(msg, {
            position: "top-center",
            timeout: 8000,
            closeOnClick: true,
            pauseOnFocusLoss: true,
            pauseOnHover: true,
            draggable: true,
            draggablePercent: 0.6,
            showCloseButtonOnHover: false,
            hideProgressBar: false,
            closeButton: "button",
            icon: true,
            rtl: false
        });
    }
};

export function errorHandler(event) {
    if (event?.status === 400 || event?.status === 401 || event?.status === 403) {

        if (event?.statusCode === 'InvalidToken') {
            notification.infoNotification(this, ' ');
            store.dispatch('logout').then(function () {
                router.push({name: 'authorization'});
            });
        }

        if (event?.statusCode === 'TokenExpired') {
            notification.infoNotification(this, ' ');
            store.dispatch('logout').then(function () {
                router.push({name: 'authorization'});
            });
        }
        if (event?.statusCode === 'NoPermissions') {
            notification.infoNotification(this, '     email ');
        }

        if (event?.statusCode === 'NotFoundError') {
            notification.infoNotification(this, '  ,   ');
        }


        return;

    }
    if (event?.status === 500) {
        notification.errorNotification(this, '');
        return;
    }

    switch (event.name) {

        case 'eventNews':
            if (event.status === 200) {
                if(event.delete){
                    notification.infoNotification(this, '');
                }else{
                    notification.infoNotification(this, '');
                }
            } else {
                if(event.delete){
                    notification.warningNotification(this, '  ');
                }else{
                    notification.warningNotification(this, '  ');
                }
            }
            break;


        case 'eventFiles':
            if (event.status === 200) {
                if(event.delete){
                    notification.infoNotification(this, '');
                }else{
                    notification.infoNotification(this, '');
                }
            } else {
                if(event.delete){
                    notification.warningNotification(this, '  ');
                }else{
                    notification.warningNotification(this, '  ');
                }
            }
            break;

        case 'eventProgram':
            if (event.status === 200) {
                notification.infoNotification(this, '');
            } else {
                notification.warningNotification(this, '  ');
            }
            break;

        case 'deleteUser':
            if (event.status === 200) {
                notification.infoNotification(this, ' ');
            } else {
                notification.warningNotification(this, '   ');
            }
            break;

        case 'notAllPass':
            notification.infoNotification(this, `     : ${event?.msg}`);
            break;

        case 'approvedRegistration':
            router.push({name: 'authorization'}).then(() => {
                if (event?.status === 200) {
                    notification.successNotification(this, ` `);
                } else {
                    notification.warningNotification(this, `  `);
                }
            })
            break;

        case 'restorePasswordSendEmail':
            router.push({name: 'authorization'}).then(() => {
                notification.infoNotification(this, `  email    `);
            })
            break;
        case 'restorePassword':
            router.push({name: 'authorization'}).then(() => {
                notification.successNotification(this, `   `);
            })
            break;

        case 'incorrectEmail':
            notification.infoNotification(this, ` email`);
            break;

        case 'streamEdit':
            notification.successNotification(this, '');
            router.push({name: 'testingList'})
            break;

        case 'imgSize':
            notification.infoNotification(this, '  ');
            break;

        case 'requiredField':
            notification.infoNotification(this, `   : ${event.msg}`);
            break;
        case 'incorrectPasswordEnter':
            notification.warningNotification(this, `  `);
            break;

        case 'changeRater':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, '');
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;
        case 'member':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, '');
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;
        case 'postMessage':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, ' ');
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;

        case 'registration':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, '   email  .         .');
                    router.push({name: 'authorization'});
                    break;
                case 402:
                    notification.warningNotification(this, '        ');
                    break;
                case 300:
                    notification.warningNotification(this, '   ');
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;
        case 'login':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, '');
                    if (localStorage.getItem('redirectRoute')) {
                        router.push(JSON.parse(localStorage.getItem('redirectRoute'))).then(() => {
                            localStorage.removeItem('redirectRoute');
                        })
                        break;
                    }
                    router.push({name: 'myEvents'});
                    break;
                // case 402:
                //     notification.warningNotification(this, ' /');
                //     break;
                default:
                    notification.errorNotification(this, '');
            }
            break;
        case 'event':
            switch (event.status) {
                case 200:
                    break;
                case 300:
                    notification.warningNotification(this, ' ');
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;

        case 'updateUser':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, '')
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;
        case 'logout':
            router.push({name: 'authorization'});
            break;

        case 'createEvent':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, '')
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;

        case 'testEditor':
            switch (event.status) {
                case 200:
                    router.push({name: 'testingList'});
                    notification.successNotification(this, '');
                    break;

                case 300:
                    notification.infoNotification(this, event.meta.body);
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;

        case 'eventEditor':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, '');
                    break;
                case 300:
                    notification.infoNotification(this, '   ');
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;

        case 'passing':
            switch (event.status) {
                case 200:
                    if (event.meta.finish) {
                        notification.infoNotification(this, ' ');
                        router.push({name: 'separateEvent', params: {eventId: event.meta.eventId}})
                    } else {
                        notification.successNotification(this, '');
                    }
                    router.go(-1)
                    break;
                case 300:
                    notification.infoNotification(this, event.meta.body);
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;
        case 'appointment':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, '');
                    break;

                case 300:
                    notification.infoNotification(this, event.meta.body);
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;
        case 'interview':
            switch (event.status) {
                case 200:
                    notification.successNotification(this, '');
                    if (event.meta.role === 'admin') {
                        router.push({name: 'testingList'})
                    }
                    break;

                case 300:
                    notification.infoNotification(this, event.meta.body);
                    break;
                default:
                    notification.errorNotification(this, '');
            }
            break;
    }
}